'use strict';

/**
* Test if a string ends with the characters of another string.
*
* @module @stdlib/string-base-ends-with
*
* @example
* import endsWith from '@stdlib/string-base-ends-with';
*
* var str = 'Fair is foul, and foul is fair, hover through fog and filthy air';
*
* var bool = endsWith( str, 'air', str.length );
* // returns true
*
* bool = endsWith( str, 'fair', str.length );
* // returns false
*
* bool = endsWith( str, 'fair', 30 );
* // returns true
*
* bool = endsWith( str, 'fair', -34 );
* // returns true
*/

// MODULES //

import HAS_BUILTIN from './has_builtin.js';
import polyfill from './polyfill.js';
import main from './main.js';


// MAIN //

var endsWith;
if ( HAS_BUILTIN ) {
	endsWith = main;
} else {
	endsWith = polyfill;
}


// EXPORTS //

export default endsWith;
